import React, { Component } from 'react'
import { Card } from 'antd'
// import echarts from 'echarts'
import echartTheme from './../themeLight'


// 
import echarts from 'echarts/lib/echarts'
// 
import 'echarts/lib/chart/line'
import 'echarts/lib/component/tooltip'
import 'echarts/lib/component/title'
import 'echarts/lib/component/legend'
import 'echarts/lib/component/markPoint'

import ReactEcharts from 'echarts-for-react'
export default class Line extends Component {
    componentWillMount() {
        echarts.registerTheme('Ire', echartTheme);
    }
    getOption = () => {
        let option = {
            title:{
                text: '',
            },
            tooltip:{
                trigger:'axis',
            },
            xAxis: {
                type: 'category',
                data: ['', '', '', '', '', '', '']
            },
            yAxis: {
                type: 'value'
            },
            series: [{
                name:"",
                data: [820, 932, 901, 934, 1290, 1330, 1320],
                type: 'line'
            }]
         
        
        }
        return option;
    }
    getOption2 = () => {
        let option = {
            title:{
                text: '',
            },
            tooltip:{
                trigger:'axis',
            },
            legend:{
                data:['ofo','']
            },
            xAxis: {
                type: 'category',
                data: ['', '', '', '', '', '', '']
            },
            yAxis: {
                type: 'value'
            },
            series: [{
                name:"ofo",
                data: [820, 932, 901, 934, 1290, 1330, 1320],
                type: 'line'
            },
            {
                name:"",
                data: [860, 972, 901, 1004, 1290, 1630, 900],
                type: 'line'
            }
        ]
         
        
        }
        return option;
    }
    getOption3 = () => {
        let option = {
            title: {
                text: ''
            },
            tooltip: {
                trigger: 'axis'
            },
            xAxis: {
                type: 'category',
                boundaryGap: false,
                data: [
                    '', '', '', '', '', '', ''
                ]
            },
            yAxis: {
                type: 'value'
            },
            series: [
                {
                    name: '',
                    type: 'line',
                    data: [
                        1000,
                        2000,
                        1500,
                        3000,
                        2000,
                        1200,
                        800
                    ],
                    areaStyle: {}
                }
            ]
        }
        return option;
    }

    render() {
        return (
            <div>
                <Card title="1">
                    <ReactEcharts option={this.getOption()} theme="Ire" style={{height:500}} />
                </Card>
                <Card title="2" style={{ marginTop: 10 }}>
                     <ReactEcharts option={this.getOption2()} theme="Ire" style={{height:500}} />
                </Card>
                <Card title="3" style={{ marginTop: 10 }}>
                     <ReactEcharts option={this.getOption3()} theme="Ire" style={{height:500}} />
                </Card>
            </div>
        )
    }
}
