import React, { Component } from 'react'
import { Card, Table, Modal, Button, message, Badge } from 'antd';
import axios from './../../axios/index'
import Utils from './../../utils/utils';
export default class HighTable extends Component {
    state = {
        dataSource: [],
        selectedRows: []
    }
    params = {
        page: 1
    }
    componentDidMount() {
        this.request()
    }
    request = () => {
        let _this = this;
        axios.axios({
            url: '/table/high/list',
            data: {
                params: {
                    page: this.params.page
                }
            }
        }).then((res) => {
            if (res.code == 0) {
                res.result.List.map((item, index) => {
                    item.key = index;
                })
                this.setState({
                    dataSource: res.result.List,
                })
            }
        })
    }
    handleChange = (pagination, filters, sorter) => {
        console.log("::" + sorter.order)
        this.setState({
            sortOrder: sorter.order
        })
    }
    handleDelete = (item)=>{
        let id = item.id;
        Modal.confirm({
            title:'',
            content:'',
            onOk:()=>{
                message.success('');
                this.request();
            }
        })
    }
    render() {
        const columns = [
            {
                title: 'id',
                dataIndex: 'id'
            },
            {
                title: '',
                dataIndex: 'userName'
            },
            {
                title: '',
                dataIndex: 'sex',
                render(sex) {
                    return sex == 1 ? '' : ''
                },
            }
            ,
            {
                title: '',
                dataIndex: 'state',
                render(state) {
                    let config = {
                        '1': '',
                        '2': '',
                        '3': '',
                        '4': '',
                        '5': ''
                    }
                    return config[state]
                },
            },
            {
                title: '',
                dataIndex: 'interest',
                render(interest) {
                    let config = {
                        '1': '',
                        '2': '',
                        '3': '',
                        '4': '',
                        '5': '',
                        '6': '',
                        '7': ''
                    }
                    return config[interest]
                },
            },
            {
                title: '',
                dataIndex: 'birthday'
            },
            {
                title: '',
                dataIndex: 'address'
            },
            {
                title: '',
                dataIndex: 'time'
            }
        ]
        const columns2 = [
            {
                title: 'id',
                dataIndex: 'id',
                fixed: 'left',
                width: 100,
            },
            {
                title: '',
                dataIndex: 'userName',
                fixed: 'left',
                width: 100,

            }, {
                title: '1',
                dataIndex: 'userName1'
            }, {
                title: '2',
                dataIndex: 'userName2'
            }, {
                title: '3',
                dataIndex: 'userName3'
            }, {
                title: '4',
                dataIndex: 'userName4'
            }, {
                title: '5',
                dataIndex: 'userName5'
            }, {
                title: '6',
                dataIndex: 'userName6'
            },
            {
                title: '',
                dataIndex: 'sex',
                render(sex) {
                    return sex == 1 ? '' : ''
                },
            }
            ,
            {
                title: '',
                dataIndex: 'state',
                render(state) {
                    let config = {
                        '1': '',
                        '2': '',
                        '3': '',
                        '4': '',
                        '5': ''
                    }
                    return config[state]
                },
            },
            {
                title: '',
                dataIndex: 'interest',
                render(interest) {
                    let config = {
                        '1': '',
                        '2': '',
                        '3': '',
                        '4': '',
                        '5': '',
                        '6': '',
                        '7': ''
                    }
                    return config[interest]
                },
            },
            {
                title: '',
                dataIndex: 'birthday'
            },
            {
                title: '',
                dataIndex: 'address'
            },
            {
                title: '',
                dataIndex: 'time'
            }
        ]
        const columns3 = [
            {
                title: 'id',
                dataIndex: 'id'
            },
            {
                title: '',
                dataIndex: 'userName'
            },
            {
                title: '',
                dataIndex: 'sex',
                render(sex) {
                    return sex == 1 ? '' : ''
                },
            },
            {
                title: '',
                dataIndex: 'age',
                sorter: (a, b) => a.age - b.age,
                sortOrder: this.state.sortOrder
            }
            ,
            {
                title: '',
                dataIndex: 'state',
                render(state) {
                    let config = {
                        '1': '',
                        '2': '',
                        '3': '',
                        '4': '',
                        '5': ''
                    }
                    return config[state]
                },
            },
            {
                title: '',
                dataIndex: 'interest',
                render(interest) {
                    let config = {
                        '1': '',
                        '2': '',
                        '3': '',
                        '4': '',
                        '5': '',
                        '6': '',
                        '7': ''
                    }
                    return config[interest]
                },
            },
            {
                title: '',
                dataIndex: 'birthday'
            },
            {
                title: '',
                dataIndex: 'address'
            },
            {
                title: '',
                dataIndex: 'time'
            }
        ]
        const columns4 = [
            {
                title: 'id',
                dataIndex: 'id'
            },
            {
                title: '',
                dataIndex: 'userName'
            },
            {
                title: '',
                dataIndex: 'sex',
                render(sex) {
                    return sex == 1 ? '' : ''
                },
            }
            ,
            {
                title: '',
                dataIndex: 'state',
                render(state) {
                    let config = {
                        '1': '',
                        '2': '',
                        '3': '',
                        '4': '',
                        '5': ''
                    }
                    return config[state]
                },
            },
            {
                title: '',
                dataIndex: 'interest',
                render(interest) {
                    let config = {
                        '1': <Badge text="" status="success" />,
                        '2': <Badge text="" status="success" />,
                        '3': <Badge text="" status="error" />,
                        '4': <Badge text="" status="success" />,
                        '5': <Badge text="" status="default" />,
                        '6': <Badge text="" status="success" />,
                        '7': <Badge text="" status="warings" />
                    }
                    return config[interest]
                },
            },
            {
                title: '',
                dataIndex: 'birthday'
            },
            {
                title: '',
                dataIndex: 'address'
            },
            {
                title: '',
                render:(text,item)=>{
                    return <Button size="small" onClick={(item) => { this.handleDelete(item) }}></Button>
                }
            }
        ]
        return (
            <div>
                <Card title="">
                    <Table
                        bordered
                        columns={columns}
                        dataSource={this.state.dataSource}
                        pagination={false}
                        scroll={{ y: 240 }}
                    />
                </Card>
                <Card title="" style={{ margin: '10px 0' }}>
                    <Table
                        bordered
                        columns={columns2}
                        dataSource={this.state.dataSource}
                        pagination={false}
                        scroll={{ x: 2500 }}
                    />
                </Card>
                <Card title="" style={{ margin: '10px 0' }}>
                    <Table
                        bordered
                        columns={columns3}
                        dataSource={this.state.dataSource}
                        pagination={false}
                        onChange={this.handleChange}
                    />
                </Card>
                <Card title="" style={{ margin: '10px 0' }}>
                    <Table
                        bordered
                        columns={columns4}
                        dataSource={this.state.dataSource}
                        pagination={false}
                    />
                </Card>
            </div>
        )
    }
}
