import React, { Component } from 'react'
import { Card, Table, Modal, Select, Button, message, Form ,DatePicker} from 'antd'
import axios from './../../axios/index'
import Utils from './../../utils/utils';
import FormItem from 'antd/lib/form/FormItem';
const { Option } = Select;

export default class City extends Component {
    componentDidMount() {
        this.requestList()
    }
    handleOpenCity = () => {
        this.setState({
            isShowOpenCity: true

        })
    }
    state = {
        list: [],
        isShowOpenCity: false
    }
    params = {
        page: 1
    }
    requestList = () => {
        let _this = this
        axios.axios({
            url: '/open_city',
            data: {
                params: {
                    page: this.params.page
                }
            }
        }).then((res) => {
            let list = res.result.item_list.map((item, index) => {
                item.key = index;
                return item;
            });
            this.setState({
                list: list,
                pagination: Utils.pagination(res, (current) => {
                    _this.params.page = current;
                    _this.requestList();
                })
            })
        })
    }
    handleSubmit = () => {
        let cityInfo = this.cityForm.props.form.getFieldsValue();
        axios.axios({
            url:"/city/open",
            data:{
                param:cityInfo
            }
        }).then((res)=>{
            if(res.code==0){
                message.success(res.result)
                this.setState({
                    isShowOpenCity:false
                })
                this.requestList()
            }
        })

    }
    render() {
        const columns = [
            {
                title: 'ID',
                dataIndex: 'id'
            }, {
                title: '',
                dataIndex: 'name'
            }, {
                title: '',
                dataIndex: 'mode',
                render(mode) {
                    return mode == 1 ? '' : '';
                }
            }, {
                title: '',
                dataIndex: 'op_mode',
                render(op_mode) {
                    return op_mode == 1 ? '' : '';
                }
            }, {
                title: '',
                dataIndex: 'franchisee_name'
            }, {
                title: '',
                dataIndex: 'city_admins',
                render(arr) {
                    return arr.map((item) => {
                        return item.user_name;
                    }).join(',');
                }
            }, {
                title: '',
                dataIndex: 'open_time'
            }, {
                title: '',
                dataIndex: 'update_time',
                render: Utils.formateDate
            }, {
                title: '',
                dataIndex: 'sys_user_name'
            }
        ]
        return (
            <div>
                <Card>
                    <FilterForm />
                </Card>
                <Card>
                    <Button type="primary" onClick={this.handleOpenCity}></Button>
                </Card>
                <div className='content-wrap'>
                    <Table
                        bordered
                        columns={columns}
                        dataSource={this.state.list}
                        pagination={this.state.pagination}
                    />
                </div>
                <Modal
                    title=""
                    visible={this.state.isShowOpenCity}
                    onCancel={() => {
                        this.setState({
                            isShowOpenCity: false
                        })
                    }}
                    onOk={this.handleSubmit}
                >
                    <OpenCityForm wrappedComponentRef={(form)=>{this.cityForm = form}}/>
                </Modal>
            </div>
        )
    }
}

class FilterForm extends Component {
    render() {
        const { getFieldDecorator } = this.props.form;
        return (
            <Form layout="inline">
                <FormItem label=''>
                    {
                        getFieldDecorator('city_id')(
                            <Select placeholder='' style={{ width: '80px' }}>
                                <Option value=""></Option>
                                <Option value="1"></Option>
                                <Option value="2"></Option>
                                <Option value="3"></Option>
                                <Option value="4"></Option>
                            </Select>)
                    }
                </FormItem>
                <FormItem label=''>
                    {
                        getFieldDecorator('mode')(
                            <Select placeholder='' style={{ width: '80px' }}>
                                <Option value=""></Option>
                                <Option value="1"></Option>
                                <Option value="2"></Option>
                            </Select>)
                    }
                </FormItem>
                <FormItem label=''>
                    {
                        getFieldDecorator('op_mode')(
                            <Select placeholder='' style={{ width: '80px' }}>
                                <Option value=""></Option>
                                <Option value="1"></Option>
                                <Option value="2"></Option>
                            </Select>)
                    }
                </FormItem>
                <FormItem label=''>
                    {
                        getFieldDecorator('auth_status')(
                            <Select placeholder='' style={{ width: '80px' }}>
                                <Option value=""></Option>
                                <Option value="1"></Option>
                                <Option value="2"></Option>
                            </Select>)
                    }
                </FormItem>
                <FormItem>
                    <Button type='primary' style={{ margin: '0 20px' }}></Button>
                    <Button></Button>
                </FormItem>
            </Form>
        )
    }
}
FilterForm = Form.create({})(FilterForm)

class OpenCityForm extends Component {
    render() {
        const { getFieldDecorator } = this.props.form;

        const formItemLayout = {
            labelCol: {
                span: 5
            },
            wrapperCol: {
                span: 19
            }
        }
        return (
            <Form layout="horizontal">
                <FormItem label="" {...formItemLayout}>
                    {
                        getFieldDecorator('city_id', {
                            initialValue: '1'
                        })(
                            <Select style={{ width: 100 }}>
                                <Option value=""></Option>
                                <Option value="1"></Option>
                                <Option value="2"></Option>
                            </Select>
                        )
                    }
                </FormItem>
                <FormItem label="" {...formItemLayout}>
                    {
                        getFieldDecorator('op_mode', {
                            initialValue: '1'
                        })(
                            <Select style={{ width: 100 }}>
                                <Option value="1"></Option>
                                <Option value="2"></Option>
                            </Select>
                        )
                    }
                </FormItem>
                <FormItem label="" {...formItemLayout}>
                    {
                        getFieldDecorator('use_mode', {
                            initialValue: '1'
                        })(
                            <Select style={{ width: 100 }}>
                                <Option value="1"></Option>
                                <Option value="2"></Option>
                            </Select>
                        )
                    }
                </FormItem>
            </Form>
        )
    }
}
OpenCityForm = Form.create({})(OpenCityForm)