import React, { Component } from 'react'
import { Card } from 'antd'
// import echarts from 'echarts'
import echartTheme from './../echartTheme'

// 
import echarts from 'echarts/lib/echarts'
// 
import 'echarts/lib/chart/bar'
import 'echarts/lib/component/tooltip'
import 'echarts/lib/component/title'
import 'echarts/lib/component/legend'
import 'echarts/lib/component/markPoint'

import ReactEcharts from 'echarts-for-react'
export default class Bar extends Component {
    componentWillMount() {
        echarts.registerTheme('Ire', echartTheme);
    }
    getOption = () => {
        let option = {
            title: {
                text: ''
            },
            tooltip: {
                trigger: 'axis'
            },
            xAxis: {
                data: ['', '', '', '', '', '', '']
            },
            yAxis: {
                type: 'value'
            },
            series: [
                {
                    name: '',
                    type: 'bar',
                    data: [1000, 2000, 1500, 3000, 2000, 1200, 800]
                }
            ]
        }
        return option;
    }
    getOption2 = () => {
        let option = {
            title: {
                text: ''
            },
            tooltip: {
                trigger: 'axis'
            },
            legend:{
                data:['ofo','','']
            },
            xAxis: {
                data: ['', '', '', '', '', '', '']
            },
            yAxis: {
                type: 'value'
            },
            series: [
                {
                    name: 'ofo',
                    type: 'bar',
                    data: [1000, 3000, 5500, 8000, 8500, 9000, 10000]
                },
                {
                    name: '',
                    type: 'bar',
                    data: [1000, 1500, 2500, 3000, 3500, 9000, 8000]
                },
                {
                    name: '',
                    type: 'bar',
                    data: [1000, 2000, 1500, 3000, 2000, 1200, 800]
                }
            ]
        }
        return option;
    }
    render() {
        return (
            <div>
                <Card title="1">
                    <ReactEcharts option={this.getOption()} theme="Ire" style={{height:500}} />
                </Card>
                <Card title="2" style={{ marginTop: 10 }}>
                     <ReactEcharts option={this.getOption2()} theme="Ire" style={{height:500}} />
                </Card>
            </div>
        )
    }
}
