import React, { Component } from 'react'
import { Card, Button,InputNumber, Icon, Input, Form, message, Checkbox, Switch, Radio, Select, DatePicker, TimePicker, Upload } from 'antd'
import moment from 'moment'
const FormItem = Form.Item
const RadioGroup = Radio.Group
const { Option } = Select;
const { TextArea } = Input;
 class Register extends Component {
     getBase64=(img, callback)=> {
        const reader = new FileReader();
        reader.addEventListener('load', () => callback(reader.result));
        reader.readAsDataURL(img);
      }
      handleChange = info => {
        if (info.file.status === 'uploading') {
          this.setState({ loading: true });
          return;
        }
        if (info.file.status === 'done') {
          // Get this url from response in real world.
          this.getBase64(info.file.originFileObj, imageUrl =>
            this.setState({
                userImg:imageUrl,
              loading: false,
            }),
          );
        }
      }
      handleSubmit=()=>{
        let uesrInfo = this.props.form.getFieldsValue();
        
      }
      state={}
    render() {
     
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol:{
                xs:24,
                sm:4,

            },
            wrapperCol:{
                xs:24,
                sm:12,
            }
        }
        const offsetLayout={
            wrapperCol:{
                xs:24,
                sm:{
                    span:12,
                    offset:4
                }
            }
        }
        return (
            <div>
                <Card title=''>
                    <Form>
                        <FormItem label="" {...formItemLayout}>
                            {
                                getFieldDecorator('userName', {
                                    initialValue: 'jack',
                                    rules: [{ required: true, message: 'Please input your username!' }],
                                })(<Input placeholder="" />)
                            }
                        </FormItem>                        
                        <FormItem label="" {...formItemLayout}>
                            {
                                getFieldDecorator('userPwd', {
                                    initialValue: '',
                                    rules: [{ required: true, message: 'Please input your userPwd!' }],
                                })(<Input placeholder="" />)
                            }
                        </FormItem>
                        <FormItem label="" {...formItemLayout}>
                            {
                                getFieldDecorator('sex', {
                                    initialValue: '1',
                                })(<RadioGroup>
                                    <Radio value="1"></Radio>
                                    <Radio value="2"></Radio>     
                                </RadioGroup>)
                            }
                        </FormItem>            
                        <FormItem label="" {...formItemLayout}>
                            {
                                getFieldDecorator('age', {
                                    initialValue: '18',
                                })(<InputNumber min={1} max={10} />)
                            }
                        </FormItem>
                        <FormItem label="" {...formItemLayout}>
                            {
                                getFieldDecorator('state', {
                                    initialValue: '1',
                                })(<Select>
                                    <Option value="1"></Option>
                                    <Option value="2"></Option>
                                    <Option value="3"></Option>
                                    <Option value="4"></Option>
                                </Select>)
                            }
                        </FormItem>      
                        <FormItem label="" {...formItemLayout}>
                            {
                                getFieldDecorator('interest', {
                                    initialValue: ['1','3'],
                                })(<Select mode='multiple'>
                                    <Option value="1"></Option>
                                    <Option value="2"></Option>
                                    <Option value="3"></Option>
                                    <Option value="4">K</Option>
                                    <Option value="5"></Option>
                                </Select>)
                            }
                        </FormItem>               
                        <FormItem label="" {...formItemLayout}>
                            {
                                getFieldDecorator('switch', {
                                    valuePropName: 'checked',
                                    initialValue: true,

                                })(<Switch  />)
                            }
                        </FormItem>
                        <FormItem label="" {...formItemLayout}>
                            {
                                getFieldDecorator('data', {
                                    
                                    initialValue: moment('2018-8-8'),

                                })( <DatePicker 
                                    style={{ width: '100%' }}
                                    showTime
                                    />)
                            }
                        </FormItem>
                        <FormItem label="" {...formItemLayout}>
                            {
                                getFieldDecorator('address', {
                                    
                                    initialValue: '',

                                })( <TextArea
                                    autoSize={{ minRows: 2, maxRows: 6 }}
                                    />)
                            }
                        </FormItem>
                        <FormItem label="" {...formItemLayout}>
                            {
                                getFieldDecorator('userImg',
                                {
                                    valuePropName: 'fileList',
                                    getValueFromEvent: this.normFile,
                                  })(<Upload  
                                    listType="picture-card"
                                    showUploadList={false}
                                    onChange={this.handleChange}
                                    action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
                                > 
                                {this.state.userImg?<img src={this.state.userImg}/>:<Icon type="plus" />}
                                </Upload>)
                            }
                        </FormItem>
                        <FormItem  {...offsetLayout}>
                            {
                                getFieldDecorator('userImg',{
                                    valuePropName: 'checked',

                                })(
                                    <Checkbox ><a href="#"></a></Checkbox>
                            )
                            }
                        </FormItem>
                        <FormItem  {...offsetLayout}>
                            {
                                getFieldDecorator('userImg')(
                                    <Button type='primary' onClick={this.handleSubmit}></Button>
                            )
                            }
                        </FormItem>
                    </Form>
                </Card>
            </div>
        )
    }
}
export default Form.create()(Register);