import React, { Component } from 'react'
import { Card, Table, Modal ,Button,message} from 'antd'
import Utils from './../../utils/utils';
import axios from './../../axios/index'

export default class BasicTable extends Component {
    // 
    state = {
        dataSource2: [],
        selectedRows:[]
    }
    params = {
        page:1
    }
    componentDidMount() {
        const dataSource = [
            {
                id: '0',
                key: '',
                userName: 'jack',
                sex: '1',
                state: '1',
                interest: '1',
                birthday: '2000-1-1',
                address: '',
                time: '9:00'
            }
        ]
        this.setState({
            dataSource
        })
        this.request()
    }
    // 
    request = ()=>{
        let _this = this;
        axios.axios({
            url:'/table/list',
            data:{
                params:{
                    page:this.params.page
                }
            }
        }).then((res)=>{
            if(res.code == 0){
                console.log(res);
                
                res.result.List.map((item, index) => {
                    item.key = index;
                })
                this.setState({
                    dataSource2:res.result.List,
                    selectedRowKeys:[],
                    selectedRows:null,
                    pagination: Utils.pagination(res,(current)=>{
                        _this.params.page = current;
                        this.request();
                    })
                })
            }
        })
    }
    handleDelete=()=>{ 
        let rows =  this.state.selectedRows
        let ids =[]
        rows.map((item)=>{
            ids.push(item.id)
        })
        Modal.confirm({
            title:'',
            content:'',
            onOk:()=>{
                message.success('');
                this.request();
            }
        })
    
       
    }
    render() {
        const columns = [
            {
                title: 'id',
                dataIndex: 'id'
            },
            {
                title: '',
                dataIndex: 'userName'
            },
            {
                title: '',
                dataIndex: 'sex',
                render(sex) {
                    return sex == 1?'':''
                },
            }
            ,
            {
                title: '',
                dataIndex: 'state',
                render(state) {
                    let config ={
                        '1':'',
                        '2':'',
                        '3':'',
                        '4':'',
                        '5':''
                    }
                    return config[state]
                },
            },
            {
                title: '',
                dataIndex: 'interest',
                render(interest) {
                    let config ={
                        '1':'',
                        '2':'',
                        '3':'',
                        '4':'',
                        '5':'',
                        '6':'',
                        '7':''
                    }
                    return config[interest]
                },
            },
            {
                title: '',
                dataIndex: 'birthday'
            },
            {
                title: '',
                dataIndex: 'address'
            },
            {
                title: '',
                dataIndex: 'time'
            }
        ]
        const rowSelection ={
            type:'radio',
            onChange: (selectedRowKeys, selectedRows) => {
                Modal.info({
                    title:'',
                    content:`:${selectedRows[0].userName},id:${selectedRows[0].id}`
                })
                console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
              },
        }
        const rowCheckSelection ={
            type:'checkbox',
            onChange: (selectedRowKeys, selectedRows) => {
                this.setState({
                    selectedRowKeys,
                    selectedRows
                })
                console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
              },
        }
        return (
            <div>
                <Card title="">
                    <Table
                        bordered
                        columns={columns}
                        dataSource={this.state.dataSource}
                        pagination={false}
                    />
                </Card>
                <Card title="" style={{ margin: '10px 0' }}>
                    <Table
                        bordered
                        columns={columns}
                        dataSource={this.state.dataSource2}
                        pagination={false}
                    />
                </Card>
                <Card title="mock-" style={{ margin: '10px 0' }}>
                    <Table
                        rowSelection={rowSelection}
                        bordered
                        columns={columns}
                        dataSource={this.state.dataSource2}
                        pagination={false}
                    />
                </Card>
                <Card title="mock-" style={{ margin: '10px 0' }}>
                    <Button type='primary' onClick={this.handleDelete}></Button>
                    <Table
                        rowSelection={rowCheckSelection}
                        bordered
                        columns={columns}
                        dataSource={this.state.dataSource2}
                        pagination={false}
                    />
                </Card>
                <Card title="mock-" style={{ margin: '10px 0' }}>
                    <Table
                   
                        bordered
                        columns={columns}
                        dataSource={this.state.dataSource2}
                        pagination={this.state.pagination}
                    />
                </Card>
            </div>
        )
    }
}
