import React, { Component } from 'react'
import {Card,Button,Icon,Radio} from 'antd'
import   './ui.css'
export default class Buttons extends Component {
    state={
        loading:true
    }
    handleCloseloading=()=>{
        this.setState({
            loading:false,
            size:'default'
        })
    }
    handleChange=(e)=>{
        this.setState({
            size:e.target.value
        })
    }
    render() {
        return (
            <div>
                <Card title="" className='card-wrap'>
                    <Button type="primary">button</Button>
                    <Button>button</Button>
                    <Button type="dashed">button</Button>
                    <Button type="danger">button</Button>
                    <Button disabled>button</Button>
                </Card>
                <Card title="" className='card-wrap'>
                    <Button icon='plus'></Button>
                    <Button icon='edit'></Button>
                    <Button icon='delete' type="dashed"></Button>
                    <Button shape="circle" icon='search'></Button>
                    <Button type="primary" icon='search'></Button>
                    <Button type="primary" icon='download'></Button>
                </Card>
                <Card title="Loaing" className='card-wrap'>
                    <Button type="primary"  loading={this.state.loading}></Button>
                    <Button type="primary" shape="circle" loading={this.state.loading}></Button>
                    <Button loading={this.state.loading}></Button>
                    <Button  shape="circle" loading={this.state.loading}></Button>
                    <Button type="primary" onClick={this.handleCloseloading}></Button>
                </Card>
                <Card title="">
                    <Button.Group>
                        <Button type="primary" >
                        <Icon type="left" /></Button>
                        <Button type="primary"><Icon type="right" /></Button>
                    </Button.Group>
                </Card>
                <Card title="" className='card-wrap'>
                    <Radio.Group value={this.state.size} onChange={this.handleChange}>
                        <Radio value='small'></Radio>
                        <Radio value='default'></Radio>
                        <Radio value='large'></Radio>
                    </Radio.Group> 
                    <Button type="primary" size={this.state.size}>button</Button>
                    <Button type="primary" size={this.state.size}>button</Button>
                    <Button type="primary" size={this.state.size}>button</Button> 
                </Card>
            </div>
        )
    }
}
